.selected-component_container {
  width: 90%;
  height: 88vh;
  background-color: rgba(0, 0, 0, 0.6);
  position: relative;
  margin-top: 2px;
  box-shadow: 
     /* 0 5px 10px rgba(255, 255, 255,.5), */ -5px 5px 10px
      rgba(255, 255, 255, 0.5),
    /* 5px -5px 10px rgba(255, 255, 255,.5), */ 5px 0 10px
      rgba(255, 255, 255, 0.5);
}

.selected-component_steps-item {
  color: #fff;
  font-size: 16px;
  font-weight: 700;
  width: 200px;
  margin-top: 20px;
  cursor: pointer;
}
.selected-component_steps-list {
  display: flex;
  width: 350px;
  margin-left: 2.5%;
  margin-top: 10px;
  flex-direction: column;
  height: 90vh;
  height: 80vh;
  overflow-y: auto;
}
.selected-component_steps-item.active {
  text-shadow: 0 5px 10px rgba(255, 255, 255, 0.7),
    -5px 5px 10px rgba(255, 255, 255, 0.7),
    5px -5px 10px rgba(255, 255, 255, 0.7), 5px 0 10px rgba(255, 255, 255, 0.7);
}
.selected-component_title {
  color: #fff;
  margin-top: 15px;
}
.selected-component_desc {
  color: #fff;
  height: 25px;
}
.selected-component_selected-item {
  color: #fff;
  border: 1px solid #fff;
  padding: 20px;
  border-radius: 5px;
  margin-top: 10px;
  cursor: pointer;
  min-width: 200px;
  margin-left: 3%;
  margin-top: 20px;
  position: relative;
  background-color: rgba(0, 0, 0, 0.8);
  max-height: 200px;
  max-width: 250px;
}

.selected-component_selected-container {
  overflow-y: auto;
  height: 65vh;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}
.selected-component_selected-item:hover {
  box-shadow: 0 5px 10px rgba(255, 255, 255, 0.5),
    -5px 5px 10px rgba(255, 255, 255, 0.5),
    5px -5px 10px rgba(255, 255, 255, 0.5), 5px 0 10px rgba(255, 255, 255, 0.5);
}
.selected-component_text-bold {
  font-weight: 900;
  padding-bottom: 5px;
  margin-top: 10px;
}
.selected-component_selected-checkbox {
  width: 20px;
  height: 20px;
  background-color: #fff;
  border-radius: 4px;
  border: 3px solid #fff;
}
.selected-component_selected-checkbox.active {
  background-color: #850685;
}
.selected-component_selected-block {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: rgba(0, 0, 0, 0.6);
}
.selected-component_selected-text {
  font-size: 14px;
  text-align: center;
}
.selected-component_btn {
  height: 40px;
  width: 100px;
  border: 1px solid #fff;
  border-radius: 6px;
  color: #fff;
  justify-content: center;
  align-items: center;
  display: flex;
  background-color: rgba(0, 0, 0, 0.5);
}
.selected-component_btn:hover {
  box-shadow: 0 5px 10px rgba(255, 255, 255, 0.5),
    -5px 5px 10px rgba(255, 255, 255, 0.5),
    5px -5px 10px rgba(255, 255, 255, 0.5), 5px 0 10px rgba(255, 255, 255, 0.5);
  cursor: pointer;
}
.selected-component_btn-cont {
  display: flex;
  width: 90%;

  justify-content: center;
  position: absolute;
  bottom: 20px;
}
.selected-component_indent {
  width: 10px;
}
.selected-component_input-cont {
  display: flex;
  justify-content: center;
}
.selected-component_input {
  all: unset;
  width: 700px;
  height: 200px;
  background-color: transparent;
  outline: none;
  border: 1px solid #fff;
  border-radius: 10px;
  color: #fff;
  padding-top: 20px;
  padding-left: 20px;
  background-color: rgba(0, 0, 0, 0.5);
}
.selected-component_input::placeholder {
  color: #fff;
}
.selected-component_selected-item.active {
  background-color: green;
}
.wrapper {
  display: flex;
  justify-content: center;
  padding-top: 72px;
}
.loader-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 200px;
}
.loader {
  width: 800px;
  height: 50px;
  background-color: transparent;
  border-radius: 10px;
  border: 2px solid #fff;
}
.loader-title {
  color: #fff;
}
.selected-component {
  display: flex;
}
.selected-component-bl {
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (max-height: 900px) {
  .selected-component_selected-container {
    height: 57vh;
  }
  .selected-component_selected-item {
    margin-left: 2%;
  }
}
@media only screen and (max-width: 600px) {
  .selected-component_container {
    width: 99%;
    height: 91vh;
  }
  .selected-component {
    flex-direction: column;
  }
  .selected-component_steps-list {
    flex-direction: row;
    height: 50px;
    overflow-x: auto;
    width: 98%;
  }
  .selected-component_steps-item {
    margin-left: 10px;
    font-size: 18px;
    white-space: nowrap;
  }
  .selected-component_desc {
    margin-top: -10px;
  }
  .selected-component-bl {
    width: 100%;
  }
  .selected-component_selected-container {
    flex-wrap: wrap;
  }
  .selected-component_selected-item {
    width: 80%;
    margin-left: 5%;
  }
  .selected-component_input {
    width: 90%;
  }
  .loader {
    width: 80%;
  }
}
