.book_book-text-cont {
  width: 75%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  margin-left: 20px;
}
.book_text_item-cont {
  overflow-y: auto;
  height: 70vh;
  margin-top: 40px;
  width: 100%;
}
.book_number-ch {
  color: #fff;
  margin-top: 10px;
  font-size: 18px;
  font-weight: 700;
}
.book_title-ch {
  color: #fff;
  margin-top: 10px;
  font-size: 20px;
  font-weight: 700;
}
.book_text_item {
  color: #fff;
  margin-top: 10px;
}
.book_edit-img {
  width: 30px;
}
.book_edit-img-cont {
  width: 40px;
  height: 30px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.book_edit-img-cont:hover {
  box-shadow: 0 5px 10px rgba(255, 255, 255, 0.5),
    -5px 5px 10px rgba(255, 255, 255, 0.5),
    5px -5px 10px rgba(255, 255, 255, 0.5), 5px 0 10px rgba(255, 255, 255, 0.5);
}
.book_title-container {
  display: flex;
  align-items: center;
  height: 50px;
  position: relative;
  width: 100%;
  justify-content: center;
}
.book_edit-img-cont {
  position: absolute;
  right: 15px;
  top: 10px;
}
.bool_text-item-cont {
  display: flex;
  position: relative;
  width: 100%;
}
.book_text_item {
  width: 90%;
}
.book_text-area-title {
  border: 1px solid #fff;
  background-color: transparent;
  border-radius: 5px;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 500px;
  font-size: 20px;
  font-weight: 700;
  margin-top: 40px;
  height: 40px;
  outline: none;
}
.book_btn {
  padding: 5px;
  color: #fff;
  border: 1px solid #fff;
  font-size: 14px;
  margin-left: 10px;
  border-radius: 5px;
  cursor: pointer;
}
.book_btn:hover {
  box-shadow: 0 5px 10px rgba(255, 255, 255, 0.5),
    -5px 5px 10px rgba(255, 255, 255, 0.5),
    5px -5px 10px rgba(255, 255, 255, 0.5), 5px 0 10px rgba(255, 255, 255, 0.5);
}
.book_btn-cont {
  display: flex;
  justify-content: flex-end;
  margin-top: 10px;
}
.book_btn-input-cont {
  display: flex;
  justify-content: flex-end;
  margin-top: 10px;
  width: 90%;
}
.book_text-input {
  width: 90%;
  background-color: transparent;
  outline: none;
  color: #fff;
  height: 400px;
  border: 1px solid #fff;
  border-radius: 5px;
  font-size: 18px;
}
.book_text-item-input-cont {
  display: flex;
  width: 100%;
  flex-direction: column;
}
.book_content-btn {
  height: 200px;
  width: 200px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border: 1px solid #fff;
  color: #fff;
  cursor: pointer;
  border-radius: 5px;
  margin-top: 15px;
  font-size: 20px;
  margin-left: 10px;
  flex-direction: column;
}
.book_content-btn:hover {
  box-shadow: 0 5px 10px rgba(255, 255, 255, 0.5),
    -5px 5px 10px rgba(255, 255, 255, 0.5),
    5px -5px 10px rgba(255, 255, 255, 0.5), 5px 0 10px rgba(255, 255, 255, 0.5);
}
.edit-book_settings {
  color: #fff;
  margin-top: 200px;
  font-size: 20px;
  font-weight: 700;
}
.book_content-btn-cont {
  width: 90%;
  display: flex;
  justify-content: flex-end;
  margin-top: 15px;
}
.book_gen-img-cont {
  display: flex;
  flex-direction: row;
  width: 90%;
  flex-wrap: wrap;
}
.book_gen-img {
  margin-left: 10px;
  cursor: pointer;
  margin-top: 15px;
  width: 200px;
  height: 200px;
}
.book_gen-img.active {
  box-shadow: 0 5px 10px rgba(255, 255, 255, 0.5),
    -5px 5px 10px rgba(255, 255, 255, 0.5),
    5px -5px 10px rgba(255, 255, 255, 0.5), 5px 0 10px rgba(255, 255, 255, 0.5);
}
.edit-book_select-cont {
  width: 80%;
  height: 40px;
  margin-top: 15px;
}
.info-btn_cont {
  color: #fff;

  height: 40px;

  font-size: 18px;
  font-weight: 700;
  margin-top: 50px;
  cursor: pointer;
}

.book_back-btn {
  color: #fff;
  border: 1px solid #fff;
  width: 200px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  font-size: 18px;
  font-weight: 700;
  margin-top: 50px;
  cursor: pointer;
  position: absolute;
  right: 10px;
  top: -15px;
}
.book_back-btn:hover {
  box-shadow: 0 5px 10px rgba(255, 255, 255, 0.5),
    -5px 5px 10px rgba(255, 255, 255, 0.5),
    5px -5px 10px rgba(255, 255, 255, 0.5), 5px 0 10px rgba(255, 255, 255, 0.5);
}
.info-container {
  border: 1px solid #fff;
  color: #fff;
  display: flex;
  flex-direction: column;
  height: 200px;
  width: 300px;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  margin-left: 10px;
}
.info {
  display: flex;
  flex-wrap: wrap;
  margin-top: 75px;
}
.info-overFlow {
  overflow-y: auto;
}
.green-img {
  width: 35px;
}
.yellow-img {
  width: 45px;
}
.red-img {
  width: 55px;
}
.show_btn {
  width: 100px;
  height: 40px;
  border: 1px solid #fff;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: "center";
  border-radius: 5px;
  position: absolute;
  bottom: 10px;
  right: 15px;
  cursor: pointer;
}
@media only screen and (max-width: 600px) {
  .book_book-text-cont {
    width: 96%;
    margin-left: 2%;
  }
  .book_text-area-title {
    width: 100%;
  }
  .book_text-input {
    width: 80%;
  }
  .book_gen-img {
    width: 100px;
    height: 100px;
  }
  .book_content-btn {
    width: 100%;
    height: 100px;
    margin-left: 10%;
  }
  .green-img {
    width: 40px;
  }
  .yellow-img {
    width: 40px;
  }
  .red-img {
    width: 40px;
  }
  .book_content-btn {
    font-size: 12px;
  }
  .img-picture {
    width: 30px;
  }
  .show_btn {
    top: 140px;
    left: 20px;
  }
  .book_title-container {
    margin-top: 20px;
  }
  .bookCont {
    width: 100%;
  }
  .edit-book_select-cont {
    position: absolute;
    top: 60px;
    z-index: 99999;
  }
  .book_number-ch {
    margin-top: 90px;
  }
  .book_text_item-cont {
    height: 55vh;
  }
}
