.backGround{
    width: 100%;
    height: 100%;
    background-color: #fdfaf7;
    color: #785e3a;
    border: 1px solid #c2b5a3;
    overflow: hidden;
    border-left: 0;
    box-shadow: inset 7px 0 30px -7px rgba(0,0,0,.4);
    display: flex;
    flex-direction: column;
    align-items: center;
}
.bookCont{
width: 740px;
margin: 0 auto;
padding-top: 95px;

}
.bookText{
    width: 80%;
    margin-top: 10px;
  
}
.bookImg{
    width: 200px;
    margin-top: 15px;
}
.getPdf{
    width: 100px;
    height: 30px;
    border: 1px solid #fff;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    position: absolute;
    bottom: 50px;
    right: 20px;
}
.popup{
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: 999;
    backdrop-filter: blur(120px);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.borderLinePopup{
    width: 450px;
    height: 40px;
    border:2px solid #fff;
    margin-top: 10px;
}
.popupText{
    color: #fff;
    font-size: 18px;
    font-weight: 600;
}
@media only screen and (max-width: 600px) {
    .book-mobile{
        height: 600px;
        overflow-y: auto;
        padding-top: 120px;
    }
    .demoPage-mob{
        height: 100%;
    }
    .borderLinePopup{
        width: 80%;
    }
}
