.my-books_book-cont {
  width: 300px;
  height: 300px !important;
  border: 3px solid #fff;
  padding: 20px;
  color: #fff;
  font-size: 18px;
  font-weight: 800;
  justify-content: center;
  align-items: center;
  display: flex;
  margin-left: 15px;
  margin-top: 15px;
  border-radius: 10px;
  transition: 0.4s;
  cursor: pointer;
}
.my-books_book-cont:hover {
  box-shadow: 0 5px 10px rgba(255, 255, 255, 0.5),
    -5px 5px 10px rgba(255, 255, 255, 0.5),
    5px -5px 10px rgba(255, 255, 255, 0.5), 5px 0 10px rgba(255, 255, 255, 0.5);
  transition: 0.4s;
}
.my-books_books-cont {
  display: flex;
  flex-direction: column;
  height: 80vh;
  overflow-y: auto;
  margin-top: 15px;
  width: 100%;
  align-items: center;
}

@media only screen and (max-width: 600px) {
  .my-books_book-cont {
    width: 80%;
    margin-left: 10%;
  }
}
