.header_container {
  width: 100%;

  height: 70px;
  background-color: rgba(0, 0, 0, 0.6);
  /* background-color: #850685; */
  position: absolute;
  display: flex;
  align-items: center;
  box-shadow: 0 15px 5px -12px rgba(255, 255, 255, 0.5);
  border-radius: 3px;
}
.header_items-block {
  display: flex;
  width: 95%;
  justify-content: flex-end;
  align-items: center;
}
.header_item {
  margin-left: 15px;
  color: #fff;
  font-size: 20px;
  font-weight: 700;
  cursor: pointer;
  transition: 0.4s;
}
.header_item:hover {
  transition: 0.4s;
  text-shadow: 0 5px 10px rgba(255, 255, 255, 0.5),
    -5px 5px 10px rgba(255, 255, 255, 0.5),
    5px -5px 10px rgba(255, 255, 255, 0.5), 5px 0 10px rgba(255, 255, 255, 0.5);
}
.header_item-img {
  margin-left: 15px;
  width: 40px;
  cursor: pointer;
}
.header_item-cent-cont {
  display: flex;
  align-items: center;
  margin-left: 30px;
}
.header_item-cent {
  color: #fff;
  margin-left: 5px;
  font-weight: 600;
}
.header_item-cent-img {
  width: 30px;
}
.header_items-block-mobile {
  display: none;
}
@media only screen and (max-width: 600px) {
  .header_items-block {
    display: none;
  }
  .header_burger-img {
    width: 40px;
    height: 40px;
  }
  .header_items-block-mobile {
    width: 94%;
    display: flex;
    margin-left: 3%;
    justify-content: space-between;
  }
  .header_items-block-left-mobile {
    display: flex;
    align-items: center;
  }
  .header_item-cent-cont {
    margin-left: 10px;
  }
}
